.crt-screen {
    position: relative;
    background-color: black;
    color: #00ff88;
    font-family: 'Press Start 2P', monospace;
    overflow: hidden;
}

    /* Horizontal scanlines */
    .crt-screen::before {
        content: '';
        pointer-events: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: repeating-linear-gradient( to bottom, rgba(0, 0, 0, 0) 0px, rgba(0, 0, 0, 0) 1px, rgba(0, 0, 0, 0.05) 2px );
        z-index: 5;
    }

    /* Glow around text */
    .crt-screen * {
        text-shadow: 0 0 2px #00ff88, 0 0 5px #00ff88, 0 0 10px #00ff88;
    }

/* Flicker animation */
@keyframes flicker {
    0%, 100% {
        opacity: 1;
    }

    50% {
        opacity: 0.98;
    }

    75% {
        opacity: 0.95;
    }
}

.crt-screen {
    animation: flicker 1s infinite;
}

.project-card {
    background-color: rgba(0, 0, 0, 0.6);
    border: 2px solid #00ff88;
    padding: 20px;
    margin: 10px;
    border-radius: 6px;
    width: 300px;
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 0 15px #00ff8855;
}

    .project-card img {
        width: 100%;
        height: auto;
        border: 2px solid #00ff88;
        margin-bottom: 10px;
    }

    .project-card .links a {
        color: #00ff88;
        margin: 5px;
        display: inline-block;
        text-decoration: none;
        font-size: 12px;
    }

    .project-card:hover {
        transform: scale(1.05);
        box-shadow: 0 0 25px #00ff88;
    }
